import app from 'flarum/forum/app';
import { extend,override } from 'flarum/extend';
import BiddingRankContentIndexPage from './components/BiddingRankContentIndexPage';
import BiddingRank from "./model/BiddingRank";
import LoadingIndicator from "flarum/components/LoadingIndicator";
import Button from 'flarum/components/Button';
import IndexPage from 'flarum/components/IndexPage';
import listItems from 'flarum/common/helpers/listItems';
import humanTime from 'flarum/common/helpers/humanTime';
import textContrastClass from 'flarum/common/helpers/textContrastClass';
import Link from 'flarum/common/components/Link';
import classList from 'flarum/common/utils/classList';

import BiddingRankContentListItem from "./components/BiddingRankContentListItem";
import BiddingRankContentSubmitModal from './components/BiddingRankContentSubmitModal';
import LogInModal from "flarum/components/LogInModal";

import TagsPage from 'flarum/tags/components/TagsPage';
import sortTags from 'flarum/tags/utils/sortTags';
import tagIcon from 'flarum/tags/helpers/tagIcon';
import tagLabel from 'flarum/tags/helpers/tagLabel';

app.initializers.add('wusong8899-client1-bidding-rank-content', () => {
  app.routes['biddingRank'] = {
    path: '/biddingRank',
    component: BiddingRankContentIndexPage,
  };

  app.store.models.biddingRankList = BiddingRank;

  let _this = TagsPage.prototype;
  extend(TagsPage.prototype, 'oninit', function () {

    _this.biddingRankLoading = true;
    _this.moreResults = false;
    _this.biddingRankList = [];
    _this.hasBid = false;
    _this.biddingRankHistoryData = {};

    _this.loadResults();
    
  });

  TagsPage.prototype.loadResults = function(offset = 0){
    const limit = 10;
    return app.store
      .find("biddingRankList", {
        page: {
          offset,
          limit
        },
      })
      .catch(() => {})
      .then(_this.parseResults.bind(_this));
  }

  TagsPage.prototype.parseResults = function(results){
    _this.moreResults = !!results.payload.links && !!results.payload.links.next;
    [].push.apply(_this.biddingRankList, results);
    _this.biddingRankLoading = false;
    m.redraw();

    return results;
  }

  TagsPage.prototype.submitContent = function(){
    if(app.session.user){
      app.modal.show(BiddingRankContentSubmitModal);
    }else{
      app.modal.show(LogInModal);
    }
  }

  TagsPage.prototype.editContent = function(){
    const itemData = _this.biddingRankHistoryData;
    app.modal.show(BiddingRankContentSubmitModal,{itemData});
  }

  TagsPage.prototype.hasMoreResults = function() {
    return _this.moreResults;
  }

  TagsPage.prototype.loadMore = function() {
    _this.biddingRankLoading = true;
    _this.loadResults(_this.biddingRankList.length);
  }

  override(TagsPage.prototype, 'view', function () {
    let rankID = 0;
    let biddingRankLoading;

    if(_this.biddingRankLoading){
      biddingRankLoading = LoadingIndicator.component({size: "large"});
    }

    if (this.loading) {
      return <LoadingIndicator />;
    }

    const pinned = this.tags.filter((tag) => tag.position() !== null);
    const cloud = this.tags.filter((tag) => tag.position() === null);

    return (
      <div className="TagsPage">
        {IndexPage.prototype.hero()}
        <div className="container">
          <nav className="TagsPage-nav IndexPage-nav sideNav">
            <ul>{listItems(IndexPage.prototype.sidebarItems().toArray())}</ul>
          </nav>

          <div className="TagsPage-content sideNavOffset">
            <ul className="TagTiles">
              {pinned.map((tag) => {
                const lastPostedDiscussion = tag.lastPostedDiscussion();
                const children = sortTags(tag.children() || []);


                return (
                  <li className={classList('TagTile', { colored: tag.color() }, textContrastClass(tag.color()))}>
                    <Link className="TagTile-info" href={app.route.tag(tag)}>
                      {tag.icon() && tagIcon(tag, {}, { useColor: false })}
                      <h3 className="TagTile-name">{tag.name()}</h3>
                      <p className="TagTile-description">{tag.description()}</p>
                      {children ? (
                        <div className="TagTile-children">
                          {children.map((child) => [<Link href={app.route.tag(child)}>{child.name()}</Link>, ' '])}
                        </div>
                      ) : (
                        ''
                      )}
                    </Link>
                    {lastPostedDiscussion ? (
                      <Link
                        className="TagTile-lastPostedDiscussion"
                        href={app.route.discussion(lastPostedDiscussion, lastPostedDiscussion.lastPostNumber())}
                      >
                        <span className="TagTile-lastPostedDiscussion-title">{lastPostedDiscussion.title()}</span>
                        {humanTime(lastPostedDiscussion.lastPostedAt())}
                      </Link>
                    ) : (
                      <span className="TagTile-lastPostedDiscussion" />
                    )}
                  </li>
                );
              })}
            </ul>

            {cloud.length ? <div className="TagCloud">{cloud.map((tag) => [tagLabel(tag, { link: true }), ' '])}</div> : ''}
          </div>


          <div className="BiddingRangContainer">
            <div className="BiddingRankListTitle selectTitle" style="height:40px;padding-top: 0px!important;;padding-bottom: 0px !important;">
              <div class="switch-btns"><div class="btns-container"><button style="width: 106px;" type="button" class="u-btn"><img src="data:image/png;base64," style="width: 15px;margin-right: 4px;vertical-align: -2px;" /><div class="u-btn-text">{app.translator.trans("wusong8899-bidding-rank.forum.bidding-rank-content")}</div></button><div id="buttonSelectedBackground" class="selected-bg" style="left: 0px; top: 0px; opacity: 1;"></div></div></div>
            </div>
            <div style="bottom: 50px;z-index: 1;width: 100%;height:10px;position: absolute;top: 13px;right: -25px;">
              {!_this.biddingRankLoading && _this.hasBid===false && (
                <Button style="position: absolute;right: 25px;font-weight: normal !important;background: #e6601b !important;" className={'Button Button--primary'} onclick={() => this.submitContent()}>
                  {app.translator.trans("wusong8899-bidding-rank.forum.submit-content")}
                </Button>
              )}

              {!_this.biddingRankLoading && _this.hasBid===true && (
                <Button style="position: absolute;right: 25px;font-weight: normal !important;background: #e6601b !important;" className={'Button Button--primary'} onclick={() => this.editContent()}>
                  {app.translator.trans("wusong8899-bidding-rank.forum.edit-content")}
                </Button>
              )}
            </div>

            <div class="BiddingRankListHeader">
              <div class="BiddingRankListHeaderContent" style="padding-left: 8px;">{app.translator.trans("wusong8899-bidding-rank.forum.bidding-rank-list-title")}</div>
              <div class="BiddingRankListHeaderMoney" style="margin-left: -5px;color:white !important">{app.translator.trans("wusong8899-bidding-rank.forum.bidding-rank-list-bid")}</div>
            </div>

            <div className="BiddingRankList">
              {_this.biddingRankList.map((biddingRankListItem) => {
                rankID++;

                return (
                  <div className="BiddingRankListItems">
                    {BiddingRankContentListItem.component({ biddingRankListItem, rankID })}
                  </div>
                );
              })}
            </div>

            {!_this.biddingRankLoading && _this.biddingRankList.length===0 && (
              <div>
                <div style="font-size:1.4em;color: var(--muted-more-color);text-align: center;height: 300px;line-height: 100px;">{app.translator.trans("wusong8899-bidding-rank.forum.list-empty")}</div>
              </div>
            )}

            {_this.hasMoreResults() && (
              <div style="text-align:center;padding:20px">
                <Button className={'Button Button--primary'} disabled={_this.biddingRankLoading} loading={_this.biddingRankLoading} onclick={() => _this.loadMore()}>
                  {app.translator.trans('wusong8899-money-leaderboard.forum.leaderboard-load-more')}
                </Button>
              </div>
            )}

            {biddingRankLoading && <div className="MoneyLeaderboard-loadMore">{biddingRankLoading}</div>}
          </div>
        </div>
      </div>
    );

  });

  // addSidebarMenu();
});